"use strict";
var form = document.querySelector('.form');
var cadastrar = document.querySelector('.cadastrar');
var cancelar = document.querySelector('.cancelar');
//Captura evento de click
cadastrar.addEventListener('click', function () {
    aluno.cadastrar();
});
cancelar.addEventListener('click', function () {
    aluno.cancelar();
});
//Previne envio do formulário
form.addEventListener('submit', function (event) {
    event.preventDefault();
});
var Aluno = /** @class */ (function () {
    function Aluno() {
        this.nome = document.querySelector('#nome');
        this.sobrenome = document.querySelector('#sobrenome');
        this.nota1 = document.querySelector('#nota1');
        this.nota2 = document.querySelector('#nota2');
        this.nota3 = document.querySelector('#nota3');
        this.listaAlunos = [];
    }
    //salva aluno no array
    Aluno.prototype.cadastrar = function () {
        if (this.validaDados()) { //chama validaDados
            var aluno_1 = this.recebeDados(); //chama recebeDados
            this.listaAlunos.push(aluno_1);
            this.limpaErro(); //limpa inputs em caso de erro na listagem anterior
            this.criaLista(); //cria lista com os dados do aluno
            this.cancelar(); //limpa o input para nova entrada
        }
        else {
            this.exibeErro();
        }
    };
    Aluno.prototype.criaLista = function () {
        var tbody = document.querySelector('#tbody');
        tbody.innerHTML = '';
        for (var i in this.listaAlunos) {
            var tr = tbody.insertRow(); //função do JS para criar nova linha dentro da tabela
            var tdNome = tr.insertCell();
            var tdMedia = tr.insertCell();
            var tdStatus = tr.insertCell();
            tdNome.innerText = this.listaAlunos[i].nomeCompleto.toUpperCase();
            tdMedia.innerText = this.listaAlunos[i].media.toFixed(2);
            tdStatus.innerText = this.listaAlunos[i].status.toUpperCase();
            //cor do status
            if (tdStatus.innerText == 'REPROVADO') {
                tdStatus.className += 'reprovado';
            }
            else if (tdStatus.innerText == 'APROVADO') {
                tdStatus.className += 'aprovado';
            }
        }
    };
    //salva dados do aluno no objeto aluno
    Aluno.prototype.recebeDados = function () {
        var aluno = {
            nome: this.nome.value,
            sobrenome: this.sobrenome.value,
            nota1: Number(this.nota1.value),
            nota2: Number(this.nota2.value),
            nota3: Number(this.nota3.value),
        };
        aluno.media = this.calculaMedia(aluno.nota1, aluno.nota2, aluno.nota3);
        aluno.status = this.confereStatus(aluno.media);
        aluno.nomeCompleto = "".concat(aluno.nome, " ").concat(aluno.sobrenome);
        return aluno;
    };
    Aluno.prototype.validaDados = function () {
        var aluno = this.recebeDados();
        if (this.nome.value == '' || this.sobrenome.value == '' || this.nota1.value == '' || this.nota2.value == '' || this.nota3.value == '')
            return false;
        return true;
    };
    Aluno.prototype.calculaMedia = function (n1, n2, n3) {
        var media = 0;
        media = ((n1 + n2 + n3) / 3);
        return media;
    };
    Aluno.prototype.confereStatus = function (media) {
        var status;
        if (media >= 6) {
            status = 'APROVADO';
        }
        else {
            status = 'REPROVADO';
        }
        return status;
    };
    Aluno.prototype.cancelar = function () {
        this.nome.value = '';
        this.sobrenome.value = '';
        this.nota1.value = '';
        this.nota2.value = '';
        this.nota3.value = '';
        this.nome.focus();
    };
    Aluno.prototype.exibeErro = function () {
        if (this.nome.value == '') {
            this.nome.className = 'erro';
        }
        if (this.sobrenome.value == '') {
            this.sobrenome.className = 'erro';
        }
        if (this.nota1.value == '' || this.nota2.value == '' || this.nota3.value == '') {
            this.nota1.className = 'erro';
            this.nota2.className = 'erro';
            this.nota3.className = 'erro';
        }
    };
    Aluno.prototype.limpaErro = function () {
        if (this.nome.value !== '') {
            this.nome.className = '';
        }
        if (this.sobrenome.value !== '') {
            this.sobrenome.className = '';
        }
        if (this.nota1.value !== '' || this.nota2.value !== '' || this.nota3.value !== '') {
            this.nota1.className = '';
            this.nota2.className = '';
            this.nota3.className = '';
        }
    };
    return Aluno;
}());
var aluno = new Aluno();
